<template>
  <div id="divChat" style="overflow-y: scroll; height:100%; width: 100%">
    <div v-for="c in chat">
      <msg :c="c">
    </div>
    <div v-if="showMensagem" class="user-msg form">
      <form class="" action="" method="post">
        <!--Quando o input é só texto-->
        <input v-focus.lazy="true" value="" type="text" :placeholder="c.placeholder" @keydown.enter.prevent="envia" v-model="mensagem"
          v-if="!c.filtro" />

        <!--Quando o input é só número-->
        <input v-focus.lazy="true" value="" type="text" :placeholder="c.placeholder" @keydown.enter.prevent="envia" v-mask="'##'" v-model="mensagem"
          v-if="c.filtro == 'numero'" />

        <!--Quando o input é em reais (R$) -->
        <input v-focus.lazy="true" value="" type="text" :placeholder="c.placeholder" @keydown.enter.prevent="envia" v-model="mensagem"
          v-if="c.filtro == 'reais'" />

        <!--Quando o input é em percentual (%) -->
        <input v-focus.lazy="true" value="" type="text" :placeholder="c.placeholder" @keydown.enter.prevent="envia" v-model="mensagem"
          v-if="c.filtro == 'percentual'" />

        <!--Quando o input é data-->
        <input v-focus.lazy="true" value="" type="text" :placeholder="c.placeholder" @keydown.enter.prevent="envia" v-mask="'##/##/####'"
          v-model="mensagem" v-if="c.filtro == 'dia'" />
          
        <button @click.prevent="envia">Enviar</button>
      </form>
    </div>
    <div v-if="showQuickButtons">
      <hr>
      <div v-for="botao in quickButtons">
        <button @click="enviaQuickButton(botao)">{{ botao }}</button>
      </div>
    </div>
  </div>
</template>

<style scoped>
  .user-msg {
    background-color: #ecf0f1;
  }
  
  .user-msg form {
    width: 700px;
    margin: 0 auto;
  }
  
  .user-msg form input {
    width: 550px;
    background-color: transparent;
    border: none;
    font: 500 22px 'Raleway', 'HelveticaNeue', arial, sans-serif;
    padding: 50px 0;
  }
  
  .user-msg form input:focus {
    outline: 0 none;
  }
  
  .user-msg form button {
    border: #27AE60 solid 2px;
    border-radius: 40px;
    padding: 15px 30px 14px 30px;
    color: #fff;
    cursor: pointer;
    background-color: #27AE60;
    font: 900 16px 'Raleway', 'HelveticaNeue', arial, sans-serif;
    transition: all .2s ease;
    box-shadow: 16px 16px 36px rgba(0, 0, 0, .1);
    margin-left: 30px;
  }
  
  @media (max-width: 750px) {
    .user-msg form {
      width: 92%;
    }
    .user-msg form input {
      width: calc(100% - 130px);
      padding: 30px 0;
    }
    .user-msg form button {
      margin-left: 10px;
    }
  }
</style>

<script>
  import { focus } from 'vue-focus'
  import bus from '../lib/bus'
  import msg from './botWeb-msg'
  import converteReal from '../lib/converteReal'
  //import MaskedInput from 'vue-masked-input'


  export default {
    props: ['script'],
    components: {
      msg,
      //MaskedInput
    },
    directives: { focus: focus },
    data() {
      return {
        //passo: 0,
        arvore: 'start',
        arvorePasso: {
        },
        showMensagem: false,
        showQuickButtons: false,
        mensagem: '',
        quickButtons: [],
        chat: [],
        c: null,
      }
    },
    mounted() {
      //alert(converteReal('5000001.23'))

      //Cria o passo de cada fluxo
      for (let fluxo of Object.keys(this.script))
        this.arvorePasso[fluxo] = 0

      //Inicia a primeira pergunta
      this.proximo()

      //Chama a proxima pergunta
      bus.$on('callProximo', (add) => {
        this.proximo(add)
      })

      //Usuario clicou em algum botao.
      bus.$on('envia', (msg) => {
        this.chat.push({ tipo: 'SENT', mt: msg })
        setTimeout(() => {
          this.proximo(true, msg)
        }, 1000)
      })

      //Mantém o chat sempre com scroll pra baixo
      this.scrollDown(30)
    },
    methods: {
      proximo(add, msgRecebida) {
        //
        // Lógica pra mudar de árvore
        //

        //Pega o "c" Anterior
        const cAnterior = this.script[this.arvore][this.arvorePasso[this.arvore]]

        //Usuario passou pra proxima pergunta...
        if (add)
          this.arvorePasso[this.arvore]++

        //usuario clicou em algum botão que é pra abrir um novo link
        // if (msgRecebida && cAnterior[`${msgRecebida}|www`] != undefined) {
        //   const link = cAnterior[`${msgRecebida}|www`]
        //   this.$router.push(link)
        // }


        //Mudança de Fluxo - Usuario clicou em Algum Botao do MENU ou do QUICKBUTTON e mudou de fluxo.
        if (msgRecebida && cAnterior[msgRecebida] && this.script[cAnterior[msgRecebida]]) {
          this.arvore = cAnterior[msgRecebida]

          // if (cAnterior[`${msgRecebida}|passo`] != undefined)
          //   this.arvorePasso[this.arvore] = cAnterior[`${msgRecebida}|passo`]
          this.arvorePasso[this.arvore] = 0
        }

        //
        // Valida se o que o usuario digitou esta correto
        //
        if (msgRecebida !== undefined && cAnterior.tipo == 'PER' && ['reais', 'numero', 'percentual'].indexOf(cAnterior.filtro) != -1 && cAnterior.max) {
          if (msgRecebida > cAnterior.max || msgRecebida < cAnterior.min) {
            this.arvorePasso[this.arvore]--
            this.arvorePasso[this.arvore]--
            this.c = cAnterior
            
            let cnt = cAnterior.cntInvalido
            if (cnt == NaN || !cAnterior.mtInvalido[cnt]) cnt = 0
            cAnterior.cntInvalido = cnt+1          
            let frase = cAnterior.mtInvalido[cnt]

            this.chat.push({ tipo: 'MSG', mt: frase })

            return
          }
        }

        //
        // Valida se o que o usuario digitou a idade correta
        //
        if (msgRecebida !== undefined && cAnterior.tipo == 'PER' && cAnterior.max && cAnterior.componente == 'idade') {

        const idade = msgRecebida
        const [dia, mes, ano] = idade.split('/').map(item => (+item))

        const jsIdadeMin = new Date(ano + cAnterior.min, mes - 1, dia)

        const jsIdadeMax = new Date(ano+cAnterior.max, mes - 1, dia)
        const jsHoje = new Date()

        // console.log('jsIdadeMax.getTime()',jsIdadeMax.getTime())
        // console.log('jsHoje.getTime()',jsHoje.getTime())

        if (jsIdadeMax.getTime() < jsHoje.getTime() || jsIdadeMin.getTime() > jsHoje.getTime()) {
            this.arvorePasso[this.arvore]--
            this.arvorePasso[this.arvore]--
            this.c = cAnterior

            let cnt = cAnterior.cntInvalido
            if (cnt == NaN || !cAnterior.mtInvalido[cnt]) cnt = 0
            cAnterior.cntInvalido = cnt+1          
            let frase = cAnterior.mtInvalido[cnt]

            this.chat.push({ tipo: 'MSG', mt: frase })

            return
          }
        }        
        

        //
        // Chama funcao quando for tipo MSG
        //
        if (cAnterior.tipo == 'MSG' && cAnterior.funcao)
          cAnterior.funcao()

        //
        // Lógica pra salvar a resposta
        //
        if (msgRecebida && cAnterior.componente) {
          console.log('Salvando Storage', cAnterior.componente, msgRecebida)
          localStorage.setItem(cAnterior.componente, msgRecebida)
        }
        
        //
        // Chama funcao pra fazer o resto das coisas
        //
        if (msgRecebida && cAnterior.funcao)
          cAnterior.funcao(msgRecebida)

        //
        // Segue o fluxo
        //
        this.c = this.script[this.arvore][this.arvorePasso[this.arvore]]

        if (!this.c) {
          console.log('terminou')
          return
        }

        if (this.c.tipo == 'GOTO') {
          this.arvore = this.c.fluxo
          this.arvorePasso[this.arvore] = 0
          this.c = this.script[this.arvore][this.arvorePasso[this.arvore]]
        }

        if (this.c.tipo == 'GOTOIF') {
          if (this.c.fluxoFn()) 
            this.arvore = this.c.seTrue
          else
            this.arvore = this.c.seFalse
        
          this.arvorePasso[this.arvore] = 0
          this.c = this.script[this.arvore][this.arvorePasso[this.arvore]]
        }

        if (this.c.tipo == 'PER') {
          this.showMensagem = true
          return
        }

        if (this.c.tipo == 'QUICKBUTTONS') {
          this.quickButtons = this.c.botoes
          this.showQuickButtons = true
          return
        }

        //this.scrollDown(20)
        this.chat.push(this.c)
      },            
      envia() {
        let mensagem = (this.mensagem + '').trim()
        let mensagemToShow = mensagem

        if (mensagem == '')
          return

        //aplica os filtros
        if (this.c.filtro == 'reais') {
          //alert(mensagem)
          mensagem = (+mensagem.replace('.', '').replace(',', '.').replace(/[^.0-9]/g, '')).toFixed(2)
          //mensagemToShow = `R$ ${mensagem.replace('.', ',')}`
          mensagemToShow = converteReal(mensagem)
        }

        if (this.c.filtro == 'percentual') {
          mensagem = ((+mensagem.replace('.', '').replace(',', '.').replace(/[^.0-9]/g, '')).toFixed(2) / 100)
          mensagemToShow = `${(mensagem * 100).toFixed(2).toString().replace('.', ',')} %`
        }

        if (this.c.filtro == 'dia') {
          const [dia, mes, ano] = mensagem.split('/')
          mensagemToShow = `${dia}/${mes}/${ano}`
        }

        this.chat.push({ tipo: 'SENT', mt: mensagemToShow })
        this.mensagem = ''
        this.showMensagem = false

        setTimeout(() => {
          this.proximo(true, mensagem)
        }, 1000)
      },
      enviaQuickButton(botao) {
        this.chat.push({ tipo: 'SENT', mt: botao })
        this.showQuickButtons = false
        this.quickButtons = []

        setTimeout(() => {
          this.proximo(true, botao)
        }, 1000)
      },
      scrollDown(delay) {
        //por algum motivo, quando EU envio mensagem, em seguida, com delay=0, da pra chamar o scrollTop...
        //quando o OUTRO USUARIO me envia mensagem, temos que dar um delayzinho.... Parece que o HTML nao foi renderizado ainda, dai chamar o scrollTop antes da renderizacao acaba nao abaixando o scroll.... Pode ser isso.
        setInterval(() => {
          document.getElementById('divChat').scrollTop = 999999
        }, delay)
      }
    }
  }

</script>


