//
// Funcoes
//
const fnPercentualContribuicaoVoluntaria = (msgRecebida) => {
    const perguntual = msgRecebida
    const contribuicaoVoluntaria = +(perguntual * localStorage.getItem('salario')).toFixed(2)
    localStorage.setItem('contribuicaoVoluntaria', contribuicaoVoluntaria)

    const resultadoContribuicao = +localStorage.getItem('resultadoContribuicao')
    localStorage.setItem('asDuasContribuicoesSomadas', resultadoContribuicao + contribuicaoVoluntaria)

}

const fnSalvaSalario = (msgRecebida) => {
    const salario = msgRecebida
    const contribuicao = (salario - 3745.57).toFixed(2)
    localStorage.setItem('salario', salario)
    localStorage.setItem('contribuicao', contribuicao)
}

const fnVerificaVoluntaria = () => {
    const percentualContribuicao = localStorage.getItem('percentualContribuicao')
    console.log('AAA percentualContribuicao ->',percentualContribuicao)

    return (percentualContribuicao == 0.075)
}

const fnSalvaPercentualContribuicao = (msgRecebida) => {
    const percentualContribuicao = msgRecebida
    const resultadoContribuicao = (percentualContribuicao * localStorage.getItem('contribuicao')).toFixed(2)
    const resultadoContribuicaoEmpresa = (1.5 * resultadoContribuicao).toFixed(2)
    localStorage.setItem('percentualContribuicao', percentualContribuicao)
    localStorage.setItem('resultadoContribuicao', resultadoContribuicao)
    localStorage.setItem('resultadoContribuicaoEmpresa', resultadoContribuicaoEmpresa)

    //zera as variaveis
    localStorage.setItem('contribuicaoVoluntaria', 0)
    localStorage.setItem('valorOutraEntidade', 0)
    localStorage.setItem('asDuasContribuicoesSomadas', resultadoContribuicao)
}

const fnCalculoFinal = () => {
    // 1. MESES = meses entre data nasc e hoje
    // 2. CTB = resultadoContribuicaoEmpresa + contribuicaoVoluntaria
    // 3. SDPJ = anosSaldoAtual * (1,004074124^MESES)
    // 4. CTBPJ = CTB * (1,004074124^MESES) + (CTB* 623,4809023)*(1,004074124^MESES - 1,00246627^MESES)
    // 5. SALDO = SDPJ + CTBPJ    

    //
    //MESES ATE SE APOSENTAR
    //
    const oneDay = 24 * 60 * 60 * 1000

    const idade = localStorage.getItem('idade')
    const [dia, mes, ano] = idade.split('/').map(item => (+item))
    const idadeAposentadoria = +localStorage.getItem('idadeAposentadoria')
    //console.log('dia, mes, ano', dia, mes, ano)

    //const dataConcessao = `${dia}/${mes}/${ano+idadeAposentadoria}`

    const jsIdade = new Date(ano, mes - 1, dia)
    //let jsDataConcessao = (new Date(ano, mes, dia)).getTime()*365.25*idadeAposentadoria
    console.log(`DEBUG - ano: ${ano} | idadeAposentadoria: ${idadeAposentadoria} | mes: ${mes} | dia: ${dia}`)
    let jsDataConcessao = new Date(ano + idadeAposentadoria, mes - 1, dia)
    //jsDataConcessao.setDate(jsDataConcessao.getDate() + 365.25*idadeAposentadoria)

    const jsHoje = new Date()

    const hojeAteAposentadoria = (jsDataConcessao.getTime() - jsHoje.getTime()) / oneDay
    const diffDias = Math.round(Math.abs((jsDataConcessao.getTime() - jsHoje.getTime()) / (oneDay)));
    const diffMeses = Math.round(diffDias / 28.09615384615385) //sei lá porque é 28.09.... Eu peguei o Excel do Fernando (simulador.xlsx), na coluna B21 tem o calculo 365.25/13
    console.log(`DEBUG - diffMeses: ${diffMeses} | diffDias: ${diffDias} | oneDay: ${oneDay} | jsDataConcessao.getTime(): ${jsDataConcessao.getTime()} | jsHoje.getTime(): ${jsHoje.getTime()}`)

    // console.log('hojeAteAposentadoria',hojeAteAposentadoria)
    // console.log('diffDias',diffDias)
    // console.log('diffMeses',diffMeses)
    // console.log('ano, mes, dia',ano, mes, dia)
    // console.log('jsIdade',jsIdade)
    // console.log('idadeAposentadoria',idadeAposentadoria)
    // console.log('jsDataConcessao',jsDataConcessao)
    // console.log('jsHoje',jsHoje)

    //
    // CTB
    //
    const resultadoContribuicao = +localStorage.getItem('resultadoContribuicao')
    const resultadoContribuicaoEmpresa = +localStorage.getItem('resultadoContribuicaoEmpresa')
    const contribuicaoVoluntaria = +localStorage.getItem('contribuicaoVoluntaria')

    const CTB = resultadoContribuicaoEmpresa + contribuicaoVoluntaria + resultadoContribuicao
    // console.log('resultadoContribuicao', resultadoContribuicao)
    // console.log('resultadoContribuicaoEmpresa', resultadoContribuicaoEmpresa)
    // console.log('contribuicaoVoluntaria', contribuicaoVoluntaria)
    // console.log('CTB', CTB)

    //
    // SDPJ
    //    
    const valorOutraEntidade = +localStorage.getItem('valorOutraEntidade')

    // let tmp = 1
    // for (let i = 0; i <= diffMeses; i++) {
    //     console.log(i,tmp)
    //     tmp = (tmp * 1.004074124)
    // }    

    const salario = +localStorage.getItem('salario')
    const expoB22 = Math.pow(1.004074124, diffMeses) //JS: 2.2006975153400545 | EXCEL:  2.20
    const SDPJ = valorOutraEntidade * expoB22

    //console.log('salario',salario)
    //console.log('expoB22',expoB22)
    // console.log('diffMeses',diffMeses)
    // console.log('valorOutraEntidade',valorOutraEntidade)
    console.log('SDPJ', SDPJ)

    //
    // CTBPJ
    //

    //const expoB23_1 = 2.19536956
    const expoB23_1 = Math.pow(1.004074124, diffMeses - 1) //JS: 2.1917679808070174 | EXCEL: 2.19536956

    //const expoB23_2 = 1.61
    const expoB23_2 = Math.pow(1.00246627, diffMeses - 1) //JS: 1.6086641486024258 | EXCEL: 1.61
    //const CTBPJ = CTB * expoB23 + (CTB * 623.4809023) * (expoB23 - Math.pow(1.00246627, diffMeses))
    console.log(`CTB: ${CTB} | expoB23_1: ${expoB23_1} | expoB23_2: ${expoB23_2}`)
    const CTBPJ = CTB * expoB23_1 + (CTB * 1.00246627) / (1.004074124 - 1.00246627) * (expoB23_1 - expoB23_2)
    //console.log('------')
    //console.log('CTB', CTB)
    // console.log('(CTB * 1.00246627)', (CTB * 1.00246627).toFixed(2))
    // console.log('expoB23_1', expoB23_1)
    // console.log('expoB23_2', expoB23_2)
    console.log('CTBPJ', CTBPJ)


    //
    // SALDO
    //
    const SALDO = (SDPJ + CTBPJ).toFixed(2)
    console.log('SALDO', SALDO)

    //
    // RENDA MENSAL INICIAL
    //
    const RENDA = (SALDO * 0.01).toFixed(2)
    //console.log('RENDA', RENDA)

    //salva o que importa pra mostrar pro usuario
    localStorage.setItem('saldo', SALDO)
    localStorage.setItem('renda', RENDA)
}

const fnClearStorage = () => {
    console.log('limpando o cache...')
    localStorage.clear()
}

//fnCalculoFinal()

module.exports = {
    //
    // FLUXO > INICIO
    //
    start: [
        { tipo: 'MSG', mt: `Oi, que bom que você se interessou e veio dar uma olhadinha em como seria o seu futuro investindo com a gente.`, funcao: fnClearStorage },
        { tipo: 'MSG', mt: `Vou te ajudar a fazer uma simulação. É bem simples. Vamos começar?` },
        menuIniciar,
        { tipo: 'MSG', mt: `Sei que não é legal sair falando por aí, mas para a simulação preciso saber sua faixa salarial. Essa informação é super confidencial, pode ficar tranquilo(a). ` },
        { tipo: 'MSG', mt: `Seu salário é maior ou menor que R$ 3.745,57?` },
        menuSalario,
        // { tipo: 'FOT', foto: `/static/teste.jpg` },
        // { tipo: 'MSG', mt: `Ótimo! Obrigado por participar. Tchau!` },
    ],

    //
    // FLUXO >>> SALARIO MENOR
    //
    salarioMenor: [
        { tipo: 'MSG', mt: `Bom, seu salário atual ainda não atingiu o piso de 1 UP (R$ 3.745,57). Questão de tempo, tenho certeza!` },
        //{ tipo: 'MSG', mt: `Mesmo assim, você pode fazer contribuições esporádicas e se beneficiar da isenção tributária de até 12% de sua remuneração. Além disso, fica coberto com auxílio doença, benefício mínimo e pensão por morte, sem desembolsar um centavo.` },
        { tipo: 'MSG', mt: `Mesmo assim, você pode fazer contribuições esporádicas e se beneficiar da isenção tributária de até 12% de sua remuneração. Além disso, é importante que você faça a adesão na CargillPrev, pois estará coberto com auxílio doença, benefício mínimo e pensão por morte, sem desembolsar um centavo.` },
        { tipo: 'MSG', mt: `Gostou? Quer saber mais sobre o plano?` },
        menuSalarioMenorSaberMais
    ],

    //
    // FLUXO >>> SALARIO MENOR >>> Não
    //
    salarioMenorNao: [
        { tipo: 'MSG', mt: `Tudo bem, mas não se esqueça que estarei sempre aqui pra te ajudar.` },
        menuReiniciaFluxo
    ],

    //
    // FLUXO >>> SALARIO MENOR >>> Sim
    //
    salarioMenorSim: [
        { tipo: 'MSG', mt: `Um bom negócio, não?` },
        { tipo: 'MSG', mt: `Faça o download da Apresentação do Plano para saber mais e, em seguida, a Adesão do plano!` },
        menuRegulamentoAderir
    ],

    //
    // FLUXO >> SALARIO MAIOR - Inicio...
    //
    salarioMaiorInicio: [
        { tipo: 'MSG', mt: `Legal. Agora, para continuar a simulação, preciso saber seu salário. O segredo continua, pode ficar tranquilo.` },
        { tipo: 'GOTO', fluxo: 'salarioMaior' }
    ],

    //
    // FLUXO >> SALARIO MAIOR - Inicio...
    //
    salarioMaiorErrado: [
        { tipo: 'MSG', mt: `Ops! Não tem problema, vamos de novo: qual é o seu salário?` },
        { tipo: 'GOTO', fluxo: 'salarioMaior' }
    ],

    //
    // FLUXO >>> SALARIO MAIOR
    //
    salarioMaior: [
        { tipo: 'PER', placeholder: 'Salário?', filtro: 'reais', min: 3745.57, max: 500000, funcao: fnSalvaSalario, mtInvalido: ['Ops, seu salário deve ser acima de R$ 3.745,57 - Vamos de novo?', 'Não não... Você deve digitar um valor acima de R$ 3.745,57 para o seu salário.', 'Humm, ainda não entendi... Tente de novo:'] },
        { tipo: 'MSG', mt: `Só pra confirmar, seu salário então é de @salario@, correto?` },
        menuConfirmaSalario
    ],

    //
    // FLUXO SALARIO MAIOR -> Continua...
    //
    salarioMaiorContinua: [
        { tipo: 'MSG', mt: `Ok, então sua base de contribuição básica é de @contribuicao@` },
        { tipo: 'MSG', mt: `Você pode fazer contribuições mensais entre 1% e 7,5%.` },
        { tipo: 'MSG', mt: `Qual seria o percentual que você gostaria de contribuir mensalmente?` },
        { tipo: 'PER', placeholder: '% de contribuição', filtro: 'percentual', min: 0.01, max: 0.075, funcao: fnSalvaPercentualContribuicao, mtInvalido: ['Ops, você tem que escolher um percentual entre 1% e 7,5%. Vamos de novo?', 'Não não... Digite, por exemplo, 5% para simular sua contribuição.', 'Humm, ainda não entendi... Tente de novo, um valor entre 1% a 7,5%.'] },
        { tipo: 'MSG', mt: `Ótimo, obrigado. Neste caso sua contribuição básica mensal seria de @resultadoContribuicao@ e a contribuição mensal da empresa seria de @resultadoContribuicaoEmpresa@ (150%)` },
        { tipo: 'MSG', mt: `E tem mais...` },
        { tipo: 'MSG', mt: `Além da contribuição básica você pode fazer voluntárias de até 10%. Só lembrando que o benefício fiscal do IRRF só vai até 12% sobre sua remuneração.` },
        { tipo: 'GOTOIF', fluxoFn: fnVerificaVoluntaria, seTrue: 'perguntaVoluntaria', seFalse: 'salarioMaiorSimulacao' },
        ,
    ],

    //
    // FLUXO >> Só pergunta a contribuição voluntária se a contribuição mensal for igual a 0.075
    //
    perguntaVoluntaria: [
        { tipo: 'MSG', mt: `Você gostaria de simular a voluntária? ` },
        menuSalarioMaiorVoluntaria
    ],

    //
    // FLUXO >>> SALARIO MAIOR >>> SIMULACAO VOLUNTARIA
    //
    salarioMaiorSimulacaoVoluntaria: [
        //start: [
        { tipo: 'MSG', mt: `Que legal. Qual seria sua opção?` },
        { tipo: 'PER', placeholder: '% de contribuição voluntária', filtro: 'percentual', min: 0.00, max: 0.10, funcao: fnPercentualContribuicaoVoluntaria, mtInvalido: ['Ops, você tem que escolher um percentual entre 0% a 10%. Vamos de novo?', 'Não não... Digite, por exemplo, 5% para simular sua contribuição voluntária.', 'Humm, ainda não entendi... Tente de novo, um valor entre 0% a 10%.'] },
        { tipo: 'MSG', mt: `Nesse caso, sua contribuição voluntária mensal seria de @contribuicaoVoluntaria@` },
        { tipo: 'GOTO', fluxo: 'salarioMaiorSimulacao' }
    ],

    //
    // FLUXO >>> SALARIO MAIOR >>> SIMULACAO
    //
    salarioMaiorSimulacao: [
        //start: [
        { tipo: 'MSG', mt: `Ok, agora vamos à simulação!` },
        { tipo: 'MSG', mt: `Prometo que não conto pra ninguém também, mas me diz: qual a sua data de nascimento?` },
        { tipo: 'PER', placeholder: 'dd/mm/yyyy', filtro: 'dia', min: 1, max: 59, componente: 'idade', mtInvalido: ['Ops, você deve ter menos de 59 anos para conseguirmos fazer a simulação. Vamos de novo, qual sua a sua data de nascimento?', 'Eu só consigo fazer a simulação se você tiver menos de 60 anos, que é a idade máxima da aposentadoria... Vamos de novo?', 'Poxa, ainda não deu. Vamos de novo:'] },
        { tipo: 'MSG', mt: `Você pretende trazer algum valor de outra entidade (banco, seguradora ou empresas)?` },
        menuTemSaldoAtual
    ],

    //
    // FLUXO >>> SALARIO MAIOR >>> SIMULACAO >>> SALDO ATUAL
    //
    salarioMaiorSimulacaoSaldoAtual: [
        { tipo: 'MSG', mt: `Legal. Qual seria esse valor?` },
        { tipo: 'PER', placeholder: 'Valor (R$) da outra entidade?', filtro: 'reais', componente: 'valorOutraEntidade', mtInvalido: ['Ops, digite novamente o saldo que você possui na outra entidade. Se não tiveres nada, apenas digite 0.'] },
        { tipo: 'GOTO', fluxo: 'salarioMaiorSimulacaoIdade' }
    ],

    //
    // FLUXO >>> SALARIO MAIOR >>> SIMULACAO >>> IDADE
    //
    salarioMaiorSimulacaoIdade: [
        //start: [
        { tipo: 'MSG', mt: `OK. Com que idade pretende se aposentar entre 55 e 60 anos? ` },
        { tipo: 'PER', placeholder: 'Idade de aposentadoria?', filtro: 'numero', min: 55, max: 60, componente: 'idadeAposentadoria', funcao: fnCalculoFinal, mtInvalido: ['Seria bom, mas você tem que escolher uma idade entre 55 e 60 anos, vamos de novo:'] },
        { tipo: 'MSG', mt: `Perfeito. Já tenho tudo o que preciso para fazer a simulação. ` },
        { tipo: 'MSG', mt: `Lembrando que é apenas uma estimativa. Vai depender do desempenho dos investimentos e do seu crescimento durante a carreira.` },
        { tipo: 'MSG', mt: `Outra coisa importante: de acordo com o regulamento do nosso plano, na aposentadoria você pode optar por sacar à vista até 25% de seu saldo e  optar por várias formas de receber o saldo remanescente. Para simplificar, optei por não sacar nada à vista e receber o saldo remanescente em parcelas mensais de 1% do saldo atualizado, ok?` },
        { tipo: 'MSG', mt: `Vamos lá. Mensalmente você estará contribuindo com @asDuasContribuicoesSomadas@ e a empresa com @resultadoContribuicaoEmpresa@.` },
        { tipo: 'MSG', mt: `Quando você completar @idadeAposentadoria@ anos, seu saldo projetado será de: @saldo@` },
        { tipo: 'MSG', mt: `Um minutinho. Vou calcular sua renda mensal e já mostro para você...` },
        { tipo: 'MSG', mt: `Sua renda mensal inicial seria de: @renda@` },
        { tipo: 'MSG', mt: `Gostou? Só em ver a simulação já dá uma tranquilidade, né? Estamos esperando você para cuidar bem do seu futuro.` },
        { tipo: 'MSG', mt: `Então, não perca tempo, faça a sua adesão agora mesmo.` },
        //menuReiniciaFluxo
        menuRegulamentoAderir
    ],
}


