<template>
    <div>
        <!-- MENSAGEM ENVIADA -->
        <div v-if="c.tipo == 'SENT'" class="msg sent">
            {{ c.mt }}
        </div>

        <!-- MENSAGEM RECEBIDA -->
        <div v-if="c.tipo == 'MSG'" class="msg sended">
            {{ mt }}
        </div>

        <!-- FOTO RECEBIDA -->
        <div v-if="c.tipo == 'FOT'" class="msg">
            <img :src="c.foto" height="200">
        </div>

        <!-- MENU RECEBIDO -->
        <div v-if="c.tipo == 'MENU' && !hidden" class="msg buttons">
            <hr>
            <div v-for="botao in c.botoes">
                <button class="btn" @click="envia(botao)" v-if="!c[botao+'|www']">{{ botao }}</button>
                <a :href="c[botao+'|www']" target="_blank" v-if="c[botao+'|www']">{{ botao }}</a>
            </div>
        </div>
    </div>
</template>

<style scoped>
    .msg {
        width: 700px;
        margin: 0 auto;
        font-size: 22px;
        line-height: 35px;
        margin-bottom: 50px;
        padding: 0 50px;
    }
    
    .msg.sended {
        background: url(../assets/ico_bot.png) left 7px no-repeat;
    }
    
    .msg.sent {
        background: url(../assets/ico_user.png) right 7px no-repeat;
        text-align: right;
        font-weight: 800;
    }
    
    .msg .btn {
        border: #27AE60 solid 2px;
        border-radius: 40px;
        padding: 10px 30px 9px 30px;
        color: #27AE60;
        cursor: pointer;
        background-color: transparent;
        font: 900 16px 'Raleway', 'HelveticaNeue', arial, sans-serif;
        transition: all .2s ease;
    }
    
    .msg .btn:hover {
        background-color: #27AE60;
        color: #fff;
        transition: all .2s ease;
        box-shadow: 16px 16px 36px rgba(0, 0, 0, .1);
    }
    
    .msg.buttons {
        padding-left: 50px;
        margin-top: -20px;
        padding-bottom: 50px;
    }
    
    .msg.buttons a {
        color: #27AE60;
        margin-bottom: 30px;
        display: inline-block;
    }
    
    .msg.buttons a:hover {
        background-color: #27AE60;
        color: #fff;
        text-decoration: none;
    }
    
    .msg.buttons div {
        float: left;
        margin-right: 15px;
    }
    
    @media (max-width: 700px) {
        .msg {
            width: 94%;
            padding: 0 0 0 40px;
        }
        .msg.sent {
            padding: 0 40px 0 0;
        }
        .msg.buttons {
            padding-left: 40px;
        }
    }
    
    @media (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi) {
        .msg.sended {
            background-image: url(../assets/ico_bot@2x.png);
            background-size: 26px 30px;
        }
        .msg.sent {
            background-image: url(../assets/ico_user@2x.png);
            background-size: 26px 30px;
        }
    }
</style>

<script>
    import bus from '../lib/bus'
    import converteReal from '../lib/converteReal'

    export default {
        props: ['c'],
        data() {
            return {
                mt: null,
                hidden: false
            }
        },
        mounted() {
            //alert(this.formatReal(5000))
            if (this.c.tipo == 'MSG') {
                let mt = this.c.mt
                if (mt.indexOf('@') > -1) {
                    const replacesReais = ['idade', 'salario', 'contribuicao', 'resultadoContribuicao', 'resultadoContribuicaoEmpresa', 'contribuicaoVoluntaria', 'asDuasContribuicoesSomadas', 'renda', 'saldo']
                    const replacesNumeros = ['idadeAposentadoria']

                    for (let item of replacesReais) {
                        let value = localStorage.getItem(item)
                        if (value)
                            mt = mt.replace(`@${item}@`, converteReal(value))
                    }

                    for (let item of replacesNumeros) {
                        let value = localStorage.getItem(item)
                        if (value)
                            mt = mt.replace(`@${item}@`, value)
                    }
                }

                this.divMSG(mt)
                return
            }

            if (this.c.tipo == 'FOT') {
                setTimeout(() => {
                    bus.$emit('callProximo', 1)
                }, 1500)
                return
            }
        },
        methods: {
            envia(msg) {
                bus.$emit('envia', msg)
                this.hidden = true
            },
            divMSG(mt) {
                this.mt = ''
                let interval = 10
                let proximoInterval
                let pos = 0
                let len = mt.length
                let ultimoFoiPontuacao = false

                let velocidadeMIN = 30
                let velocidadeMAX = 55

                if (window.location.hostname.toLowerCase().indexOf('localhost') > -1) {
                    velocidadeMIN = 1
                    velocidadeMAX = 4
                }

                const proximaLetra = () => {
                    setTimeout(() => {
                        this.mt += mt[pos++]

                        //pra descer o scroll caso haja muito texto, quebrado o texto em 2 linhas...
                        if (mt[pos] == ' ')
                            this.scrollDown()

                        interval = proximoInterval
                        if (mt[pos] && ['.', ',', '!', '?'].indexOf(mt[pos]) > -1 && !ultimoFoiPontuacao) {
                            proximoInterval = Math.floor(Math.random() * (velocidadeMAX * 10 - velocidadeMIN * 10 + 1)) + velocidadeMIN * 10
                            ultimoFoiPontuacao = true
                        } else {
                            proximoInterval = Math.floor(Math.random() * (velocidadeMAX - velocidadeMIN + 1)) + velocidadeMIN
                            ultimoFoiPontuacao = (mt[pos] && ['.', ',', '!', '?'].indexOf(mt[pos]) > -1)
                        }

                        if (!mt[pos]) {
                            setTimeout(() => {
                                bus.$emit('callProximo', 1)
                            }, 300)
                            return
                        }

                        proximaLetra()
                        this.scrollDown()
                    }, interval)
                }

                proximaLetra()
            },
            scrollDown() {
                document.getElementById('divChat').scrollTop = 999999

            }
        }
    }

</script>


